import './WeatherForecast.css';

import { Box, Container, Divider, experimental_sx, Fade, Grid, Stack, styled, Typography } from '@mui/material';
import jaLocale from 'apexcharts/dist/locales/ja.json';
import Leaflet, { LatLng } from 'leaflet';
import React, { useEffect, useState } from 'react';
import Chart from 'react-apexcharts';
import { MapContainer, Marker, TileLayer } from 'react-leaflet';

import imageCloudy from '../assets/cloudy.webp';
import imageFewCloudy from '../assets/few-cloudy.webp';
import imageFoggy from '../assets/foggy.webp';
import imageRainy from '../assets/rainy.webp';
import imageSnowy from '../assets/snowy.webp';
import imageStormy from '../assets/stormy.webp';
import imageSunny from '../assets/sunny.webp';
import { useWeatherForecast } from '../hooks';

Leaflet.Icon.Default.imagePath = '//cdnjs.cloudflare.com/ajax/libs/leaflet/1.8.0/images/';

const Background = styled(Box)(
  experimental_sx({
    position: 'relative',
    minHeight: '100vh',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    backgroundAttachment: 'fixed',
  })
);

const DarkRoundBox = styled(Box)(
  experimental_sx({
    bgcolor: '#254F8FCC',
    borderRadius: 2,
    boxShadow: 1,
  })
);

// eslint-disable-next-line complexity
export const WeatherForecast = () => {
  const [coord, setCoord] = useState<LatLng>(new LatLng(35.69, 139.69));
  const weather = useWeatherForecast(coord);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((pos) => {
      setCoord(new LatLng(pos.coords.latitude, pos.coords.longitude));
    });
  }, [setCoord]);

  const chartWeather = weather?.forecasts.slice(0, 9);
  const maxTemperature =
    chartWeather && chartWeather.map((f) => f.temperature).reduce((_1, _2) => Math.max(_1, _2)) + 1;
  const minTemperature =
    chartWeather && chartWeather.map((f) => f.temperature).reduce((_1, _2) => Math.min(_1, _2)) - 1;
  const maxHumidity =
    chartWeather &&
    Math.min((Math.floor(chartWeather.map((f) => f.humidity).reduce((_1, _2) => Math.max(_1, _2)) / 10) + 1) * 10, 100);
  const minHumidity =
    chartWeather &&
    Math.max((Math.floor(chartWeather.map((f) => f.humidity).reduce((_1, _2) => Math.min(_1, _2)) / 10) - 1) * 10, 0);

  if (!weather || !chartWeather) return <div />;

  return (
    <Fade in={true} timeout={1500}>
      <Background
        sx={{
          backgroundImage: () => {
            switch (weather.forecasts[0].group) {
              case 'Thunderstorm':
                return `url(${imageStormy})`;
              case 'Drizzle':
                return `url(${imageRainy})`;
              case 'Rain':
                return `url(${imageRainy})`;
              case 'Snow':
                return `url(${imageSnowy})`;
              case 'Atmosphere':
                return `url(${imageFoggy})`;
              case 'Clear':
                return `url(${imageSunny})`;
              case 'Clouds':
                return `url(${imageCloudy})`;
              case 'FewClouds':
                return `url(${imageFewCloudy})`;
              default:
                return `url(${imageSunny})`;
            }
          },
        }}
      >
        <Container sx={{ pt: { xs: 8, sm: 16 }, pb: { xs: 4, sm: 8 } }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={7} order={0}>
              <DarkRoundBox sx={{ p: { xs: 1.5, sm: 4 } }}>
                <Typography sx={{ fontSize: 20, fontWeight: 'bold' }}>{weather.city} の天気</Typography>
                <Typography sx={{ fontSize: 16 }}>{`${weather.forecasts[0].date
                  .getHours()
                  .toString()
                  .padStart(2, '0')}:${weather.forecasts[0].date
                  .getMinutes()
                  .toString()
                  .padStart(2, '0')}`}</Typography>
                <Grid container alignItems="center">
                  <Grid item sx={{ mx: 1 }}>
                    <Stack direction="row" alignItems="center" divider={<Divider orientation="vertical" />}>
                      <img src={weather.forecasts[0].iconUrl} />
                      <Typography sx={{ fontSize: 62 }}>{weather.forecasts[0].temperature}°</Typography>
                    </Stack>
                  </Grid>
                  <Grid item sx={{ mx: 1 }}>
                    <Stack>
                      <Typography sx={{ fontSize: 22, fontWeight: 'bold' }}>
                        {weather.forecasts[0].description}
                      </Typography>
                      <Typography sx={{ fontSize: 16 }}>体感温度 {weather.forecasts[0].feelsLike}°</Typography>
                    </Stack>
                  </Grid>
                </Grid>
                <Grid container alignItems="center" sx={{ mt: 1 }}>
                  <Grid item sx={{ mx: 1 }}>
                    <Stack>
                      <Typography sx={{ fontSize: 13 }}>風速</Typography>
                      <Typography sx={{ fontSize: 20 }}>{weather.forecasts[0]?.windSpeed}m/s</Typography>
                    </Stack>
                  </Grid>
                  <Grid item sx={{ mx: 1 }}>
                    <Stack>
                      <Typography sx={{ fontSize: 13 }}>湿度</Typography>
                      <Typography sx={{ fontSize: 20 }}>{weather.forecasts[0]?.humidity}%</Typography>
                    </Stack>
                  </Grid>
                  <Grid item sx={{ mx: 1 }}>
                    <Stack>
                      <Typography sx={{ fontSize: 13 }}>視界</Typography>
                      <Typography sx={{ fontSize: 20 }}>
                        {weather.forecasts[0]?.visibility < 1000
                          ? `${weather.forecasts[0]?.visibility}m`
                          : `${weather.forecasts[0]?.visibility / 1000}km`}
                      </Typography>
                    </Stack>
                  </Grid>
                  <Grid item sx={{ mx: 1 }}>
                    <Stack>
                      <Typography sx={{ fontSize: 13 }}>気圧</Typography>
                      <Typography sx={{ fontSize: 20 }}>{weather.forecasts[0]?.pressure}hPa</Typography>
                    </Stack>
                  </Grid>
                </Grid>
              </DarkRoundBox>
            </Grid>

            <Grid item xs={12} sm={5} order={{ xs: 2, sm: 1 }}>
              <DarkRoundBox sx={{ height: { xs: '140px', sm: '100%' } }}>
                <MapContainer center={coord} zoom={13} scrollWheelZoom={false}>
                  <TileLayer
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a>'
                  />
                  <Marker position={coord} />
                </MapContainer>
              </DarkRoundBox>
            </Grid>

            <Grid item xs={12} order={{ xs: 1, sm: 2 }}>
              <DarkRoundBox>
                <Chart
                  series={
                    [
                      { name: '気温', data: chartWeather.map((f) => [f.date, f.temperature]) },
                      { name: '湿度', data: chartWeather.map((f) => [f.date, f.humidity]) },
                    ] as ApexAxisChartSeries
                  }
                  height="100%"
                  options={{
                    chart: {
                      defaultLocale: 'ja',
                      locales: [jaLocale],
                      toolbar: { show: false },
                      zoom: { enabled: false },
                      animations: {
                        easing: 'easeinout',
                        animateGradually: { enabled: false },
                        dynamicAnimation: { speed: 100 },
                      },
                    },
                    xaxis: {
                      type: 'datetime',
                      labels: {
                        style: { colors: 'white' },
                        format: 'HH:mm',
                        datetimeUTC: false,
                      },
                      tooltip: {
                        enabled: false,
                      },
                    },
                    yaxis: [
                      {
                        labels: {
                          style: { colors: 'white' },
                          formatter: (val) => `${val.toFixed(0)}°`,
                        },
                        tickAmount: 4,
                        min: minTemperature,
                        max: maxTemperature,
                      },
                      {
                        labels: {
                          style: { colors: 'white' },
                          formatter: (val) => `${val.toFixed(0)}%`,
                        },
                        tickAmount: 4,
                        min: minHumidity,
                        max: maxHumidity,
                        opposite: true,
                      },
                    ],
                    tooltip: {
                      theme: 'dark',
                      x: { format: 'yyyy/MM/dd HH:mm' },
                    },
                    stroke: {
                      curve: 'smooth',
                      width: [3, 3],
                      dashArray: [0, 3],
                    },
                    colors: ['#7FFFBF', '#BF7FFF'],
                    legend: { show: false },
                  }}
                />
              </DarkRoundBox>
            </Grid>
          </Grid>
        </Container>
      </Background>
    </Fade>
  );
};
